'use strict';

app.common = {
	text: {
		title: 'Внимание!',
		info: 'Вы попытались попасть на LOOT.BET - сайт, созданный для ставок на киберспорт, с кучей лайв стримов, бонусов, кэшбеком и даже кэшаутом.',
		blockText: 'У вас не получится войти пока вы делаете это отсюда: ',
		country: 'Страна:'
	},
	init: function init() {
		if (this.initCountry() === 'en') {
			app.common.rusTranslate();
		}
		this.reloadBaseVh();
		this.rotateChangeHeight();
	},
	initCountry: function initCountry() {
		var client = window.navigator.language || window.navigator.systemLanguage || window.navigator.userLanguage;
		var language = client.search('-') > 0 ? client.substring(0, client.search('-')).toLowerCase() : client.toLowerCase();
		return language;
	},
	changeText: function changeText(el, text) {
		document.querySelector(el).innerHTML = text;
	},
	rusTranslate: function rusTranslate() {
		this.changeText('.content__title', app.common.text.title);
		this.changeText('.content__text-info', app.common.text.info);
		this.changeText('.content__text-blocked', app.common.text.blockText);
		this.changeText('.content__data-title_country', app.common.text.country);
	},
	rotateChangeHeight: function rotateChangeHeight() {
		var _this = this;

		window.addEventListener('resize', function () {
			_this.reloadBaseVh();
		});
	},
	reloadBaseVh: function reloadBaseVh() {
		var vh = window.innerHeight * 0.01;
		document.documentElement.style.setProperty('--vh', vh + 'px');
	}
};